import React, { useState, useEffect, createRef } from "react"
import styled from "@emotion/styled"
import {
  InstantSearch,
  connectStateResults,
  connectHits
} from "react-instantsearch-dom"
import algoliasearch from "algoliasearch/lite"
import PostHit from "./searchResult"
import Input from "./Input"


const Root = styled.div`
  position: relative;
  display: grid;
  grid-gap: 10px;
`

const HitsWrapper = styled.div`
  display: flex;
  flex-direction: column;
  height: calc(100vh - 52px);
  
  @media only screen and (max-width: 768px) {
     width: 100vw;
  }
`

//number of result
const Stats = connectStateResults(
  ({ searchResults: res }) =>
    res && `${res.nbHits} result${res.nbHits > 1 ? `s` : ``}`
)

const ResultHeader = styled.div`
  background-color: #3c3c3c;
  color: white;
  display: block;
  font-size: larger;
  padding: 5px 10px;
`

//if clicking outside then remove?
const useClickOutside = (ref, handler, events) => {
  if (!events) events = [`mousedown`, `touchstart`]
  const detectClickOutside = event =>
    ref.current && !ref.current.contains(event.target) && handler()

  useEffect(() => {
    for (const event of events)
      document.addEventListener(event, detectClickOutside)

    return () => {
      for (const event of events)
        document.removeEventListener(event, detectClickOutside)
    }
  })
}


const CustomHits = connectHits(({ hits, foldSearch }) => (
  <ul>
    {hits.map(hit => (
      <li>
        <PostHit hit={hit} foldSearch={foldSearch} />
      </li>
    ))}
  </ul>
));


const ResultWrapper = styled.div`
  background-color: rgba(59,59,59,0.7);
  height: 100%;
  overflow-y: auto;
  width: 100%;
  padding-bottom: 35px;
  ul {
    list-style: none;
    padding: 5px 10px;
    margin-top: 0;
  }
  li {
    margin: 0 0 10px;
  }
`

const CloseButton = styled.div`
  float: right;
  display: inline-block;
  color: #f1f1f1;
  cursor: pointer;
  font-size: x-large;
`

export default function Search({ indices, collapse, hitsAsGrid, foldSearch }) {
  const ref= createRef()
  const [query, setQuery] = useState(``)
  const [focus, setFocus] = useState(false)
  const searchClient = algoliasearch(
    process.env.GATSBY_ALGOLIA_APP_ID,
    process.env.GATSBY_ALGOLIA_SEARCH_KEY
  )

  useClickOutside(ref, () =>  {setFocus(false); foldSearch()})
  console.log("rendered");
  return (
    <InstantSearch
      searchClient={searchClient}
      indexName="Blog"
      onSearchStateChange={({ query }) => setQuery(query)}
      root={{ Root, props: { ref } }}
    >
      <Input onFocus={() => setFocus(true)} {...{ collapse, focus }} />
      {query.length > 2 && focus && (
        <HitsWrapper >
          <ResultHeader>
            <Stats />
            <CloseButton onClick={() => {setFocus(false); foldSearch()}}>×</CloseButton>
          </ResultHeader>
          <ResultWrapper><CustomHits key="searchBar" foldSearch={foldSearch}/></ResultWrapper>
        </HitsWrapper>
      )}
    </InstantSearch>
  )
}